import React from "react";
import logo from "../../images/logo.svg";
import "./Header.css";
import { Link, useRouteMatch } from 'react-router-dom'; 

function Header(props) {
  const { path, url } = useRouteMatch();
  
  const headerClassName = `header ${ 
    props.history.location.pathname === "/cart" || 
    props.history.location.pathname === "/confirmation" || 
    props.history.location.pathname === "/login" || 
    props.history.location.pathname === "/add-product" ?
    `header_hidden` :
    ``
}`
  return (
    <header className={headerClassName}>
      <Link exact="true" to="/">
        <img alt=" " src={logo} className="header__logo"></img>
      </Link>
      <div className="header__nav">
        <ul
          className="header__nav"
          aria-labelledby="menu"
          aria-label="submenu"
        >
          <li className="header__nav-element">
            <Link to="/catalog"></Link>
          </li>
          <li className="header__nav-element">
            <a href="#">  </a>
          </li>
          <li className="header__nav-element">
            <a href="#"></a>
          </li>
          <li className="header__nav-element">
            <a href="#">  </a>
          </li>
          <li className="header__nav-element">
            <a href="#"></a>
          </li>
          <li className="header__nav-element">
            <a href="#"></a>
          </li>
        </ul>
      </div>
      <a href="tel:+7123456789" className="header__number">+7 123 456-78-90</a>
      <div className="header__menu-icon" onClick={props.changeNavState}></div>
    </header>
  );
}

export default Header;
